
import { defineComponent } from "@vue/runtime-core";

import Buttons from './Buttons.vue'
import DataService from '@/services/DataService';
import Data from "@/interfaces/Data";
import Options from "@/interfaces/Options";
import ResponseData from "@/services/ResponseData";
import ChartLegend from './ChartLegend.vue'
export default defineComponent({
    name: 'LineChart',
    components: {Buttons, ChartLegend},
    props: {
        id: Number
    },
    data: () => ({
        data: {} as Data[],
        options: {} as Options,
        series: [] as any,
        limit: 200 as number,
        dateFrom: "11/10/2021" as string,
        dateTo: '06/30/2021' as string,
        x: '' as string,
        y: '' as string,
        loading: true as boolean
    }),
    created() {
        this.getChartData()
    },
    methods: {
        changeOptions(data: any){
            console.log(data)
        },
        changeSettings(data: any){
            console.log(data)
        },
        getChartData(){
        this.loading = true
        DataService.getData(this.dateFrom, this.dateTo, this.limit).then( (response: ResponseData<Data[]>) => {
           let data = response.data.map( obj => obj.temperature)
           let categories = response.data.map(obj => obj.datetime)
           let what = {  chart: {
              type: 'line',
              stacked: false,
              height: 350,
              zoom: {
                type: 'x',
                enabled: true,
                autoScaleYaxis: true
              },
              toolbar: {
                autoSelected: 'zoom'
              }
            },
            xaxis: {
              categories: categories
            },
            }
            this.options = Object.assign({},what)
            this.series = [
                {
                    name: 'Temperature',
                    data: data
                }
            ]
            this.loading = false;
        }) 
        }
    }
})
